.meme {
  display: flex;
  position: relative;
  width: 90%;
  margin: auto;
  justify-content: center;
  align-items: baseline;
}

.meme > img {
  width: 80%;
  max-width: 50em;
  margin-left: 2em;
  margin-bottom: 3em;
  height: auto;
  background: cover;
  background-repeat: no-repeat;
}

.meme h2 {
  position: absolute;
  max-width: 80%;
  text-align: center;
  flex-wrap: wrap;
  left: 50%;
  transform: translateX(-50%);
  margin: 15px 0;
  padding: 0 0px;
  font-family: impact, sans-serif;
  text-transform: uppercase;
  color: white;
  text-shadow: 2px 2px 0 #000, -2px -2px 0 #000, 2px -2px 0 #000,
    -2px 2px 0 #000, 0 2px 0 #000, 2px 0 0 #000, 0 -2px 0 #000, -2px 0 0 #000,
    2px 2px 5px #000;
}

.meme > .bottom {
  bottom: 40px;
}

.meme > .top {
  top: 0px;
}

.meme-form {
  width: 100%;
  max-width: 60em;
  margin: 1.5em 0.5em;
  display: block;
  justify-content: space-between;
}

.meme-form > input {
  border: 2px solid;
  border-radius: 5px;
  margin-left: 4.9em;
  display: block;
  width: 60%;
  height: 40px;
}

.meme-form > button {
  height: 1.8em;
  width: 6.5em;
  margin-top: 0.5em;
  margin-left: 2.5em;
  border: 3px rgb(221, 210, 210) solid;
  font-family: VT323, monospace;
  font-size: 1.8em;
  letter-spacing: 1.5px;
  color: white;
  background: rgb(184, 56, 56);
}
.meme-form > button:hover {
  background: rgba(184, 56, 56, 0.8);
}

.meme-form > input::-webkit-input-placeholder {
  /* Chrome/Opera/Safari */
  font-family: VT323, monospace;
  font-size: 1.3em;
  text-align: left;
}
.meme-form > input::-moz-placeholder {
  /* Firefox 19+ */
  font-family: VT323, monospace;
  font-size: 25px;
  text-align: left;
}
.meme-form > input:-ms-input-placeholder {
  /* IE 10+ */
  font-family: VT323, monospace;
  font-size: 25px;
  text-align: left;
}
.meme-form > input:-moz-placeholder {
  /* Firefox 18- */
  font-family: VT323, monospace;
  font-size: 25px;
  text-align: left;
}
